@import url('https://fonts.googleapis.com/css2?family=Trispace:wght@200;300;400;500&display=swap');
@import url('./start.scss');

$first-color: #353d5b;
$second-color: #1f2833;
$third-color: #c5c6c7;
$fourth-color: #66fcf1;
$fifth-color: #42a29e;

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Trispace';
    color:#fff;
}

section{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $first-color;
}

.container{
    padding: 150px;
}


body{
    color:black;
}
